.welcomeheader {
  width: 100%;
  height: 100vh;
  color: white;
  background-color: rgba(0, 0, 0, 0.65);
  background-image: url("F:\my_site\src\AllFiles\code-wallpaper-9.jpg");
  /* background-size: unset;

  color: rgb(211, 82, 243); */
}

.h1 {
  text-align: center;
  font-size: 3em;
  font-weight: bold;
  text-decoration: blue;
}
.p {
  text-align: left;
}

.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.span {
  display: block;
  width: 30px;
  height: 30px;
  border-bottom: 5px solid #06a8ff;
  border-right: 5px solid #06a8ff;
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}
.span:nth-child(2) {
  animation-delay: -0.2s;
}
.span:nth-child(3) {
  animation-delay: -0.4s;
}
@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}
